import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import StarIcon from '@material-ui/icons/StarBorder';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { logoutHandler } from './userReducer';
import { connect } from 'react-redux'
import Appbar from './Appbar'
import { Avatar } from '@material-ui/core';
import Axios from 'axios';


const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  appBar: {
    borderBottom: `1px solid ${theme.palette.divider}`,
  },
  toolbar: {
    flexWrap: 'wrap',
  },
  toolbarTitle: {
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),
  },
  heroContent: {
    padding: theme.spacing(8, 0, 6),
  },
  cardHeader: {
    backgroundColor: "lightblue"
  },
  cardPricing: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'baseline',
    marginBottom: theme.spacing(2),
  },
  footer: {
    borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('sm')]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
  },
}));

function Home(props) {
  const classes = useStyles();
  const [username, setUsername] = React.useState([{ fname: "", lname: "" }])
  const [picture, setPicture] = React.useState()
  const [notes, setNotes] = React.useState()
  React.useEffect(() => {
    Axios.post('http://localhost:3001/profile', {
      username: localStorage.getItem('username')
    }
    ).then((res) => {
      console.log(res.data);
      setUsername(res.data.result);
      setPicture(res.data.picture);
      setNotes(res.data.result1[0].count)
    }).catch((err) => {
      alert(err.response.message)
    })
  }, [])
  return (
    <React.Fragment>
      <CssBaseline />

      {/* Hero unit */}
      <Container maxWidth="sm" component="main" className={classes.heroContent}>

        <Typography variant="h3" align="center" component="p">
          Your Profile
        </Typography>
        <Card style={{ margin: "15%" }}>
          <CardHeader
            title={username[0].fname + " " + username[0].lname}
            subheader={username[0].username}
            titleTypographyProps={{ align: 'center' }}
            subheaderTypographyProps={{ align: 'center' }}
            className={classes.cardHeader}
          />
          <CardContent style={{ backgroundColor: "lightgrey" }}>
            <Avatar src={picture} style={{ display: "block", margin: "auto", height: "20vh", width: "20vh" }} />
            <br />
            <Typography align="center" component="p">
              Notes: {notes}
            </Typography>
          </CardContent>

        </Card>
      </Container>
      <br />
      <br />
      <br />
      {/* End hero unit */}


    </React.Fragment>
  );
}

const mapStateToProps = (state) => {
  return {

  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    logoutHandler: () => { dispatch(logoutHandler()) }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);